@import "components/header";
@import "components/asside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-9{
          .products{
            &__compare{
              display: flex;
              justify-content: flex-end;
              .mobileSort{
                display: none;
                flex-direction: column;
                position: relative;
                #sorting{
                  padding: 0 25px 0 0;
                  display: block;
                  font: .875em/3em "Arial";
                  color: #0081ef;
                  &:hover{
                    cursor: pointer;
                  }
                }
                .sortOptions{
                  position: absolute;
                  top: 35px;
                  background-color: #fff;
                  white-space: nowrap;
                  z-index: 5;
                  .filter{
                    margin: 10px;
                    display: flex;
                    align-items: center;
                    p{
                      margin: 0 15px 0 0;
                      font: 1em/1.125em "Arial";
                      font-weight: bold;
                      color: #777;
                    }
                    span{
                      .ms-options-wrap{
                        button{
                          width: 175px;
                          font: .938em/1.063em "Arial";
                          color: #434343;
                          background-color: #eaeaea;
                          border-color: #bdbdbd;
                          outline: none;
                          &:hover{
                            cursor: pointer;
                          }
                          &:after{
                            border-top-color: #434343;
                          }
                        }
                        .ms-options{
                          padding: 5px 0 5px 15px;
                          width: 180px;
                          font: .938em/1.063em "Arial";
                          color: #434343;
                          left: 102px;
                          background-color: #eaeaea;
                          border-color: #bdbdbd;
                          overflow: unset;
                          ul{
                            label{
                              background-color: transparent;
                              border: none;
                            }
                          }
                        }
                        .ms-options.hide-checkbox{
                          ul{
                            input[type="checkbox"]{
                              overflow: unset;
                            }
                          }
                        }
                      }
                    }
                  }
                  .sortBy{
                    display: flex;
                    flex-direction: column;
                    .sort,
                    .quantity{
                      margin: 10px;
                      display: flex;
                      justify-content: space-between;
                      align-items: center;
                      position: relative;
                      p{
                        width: 33%;
                        font: 1em/1.125em "Arial";
                        font-weight: bold;
                        color: #777;
                      }
                      select{
                        padding: 0 10px;
                        height: 25px;
                        font: .938em/25px "Arial";
                        color: #434343;
                        background-color: #eaeaea;
                        border: 1px solid #bdbdbd;
                        outline: none;
                        -webkit-appearance: none;
                        -moz-appearance: none;
                        -ms-appearance: none;
                        appearance: none!important;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                      select::-ms-expand{
                        display: none;
                      }
                      i{
                        position: absolute;
                        top: 6px;
                        right: 7px;
                      }
                    }
                    .sort{
                      select{
                        width: 175px;
                      }
                    }
                    .quantity{
                      select{
                        width: 175px;
                      }
                    }
                  }
                }
              }
              a{
                display: block;
                font: .875em/3em "Arial";
                color: #0081ef;
                text-decoration: none;
              }
            }
            &__sortOptions{
              display: flex;
              justify-content: space-between;
              .filter{
                display: flex;
                align-items: center;
                p{
                  margin: 0 15px 0 0;
                  font: 1em/1.125em "Arial";
                  font-weight: bold;
                  color: #777;
                }
                span{
                  .ms-options-wrap{
                    button{
                      width: 180px;
                      font: .938em/1.063em "Arial";
                      color: #434343;
                      background-color: #eaeaea;
                      border-color: #bdbdbd;
                      outline: none;
                      &:hover{
                        cursor: pointer;
                      }
                      &:after{
                        border-top-color: #434343;
                      }
                    }
                    .ms-options{
                      padding: 5px 0 5px 15px;
                      width: 180px;
                      font: .938em/1.063em "Arial";
                      color: #434343;
                      left: 102px;
                      background-color: #eaeaea;
                      border-color: #bdbdbd;
                      overflow: unset;
                      ul{
                        label{
                          background-color: transparent;
                          border: none;
                        }
                      }
                    }
                    .ms-options.hide-checkbox{
                      ul{
                        input[type="checkbox"]{
                          overflow: unset;
                        }
                      }
                    }
                  }
                }
              }
              .sortBy{
                display: flex;
                .sort,
                .quantity{
                  display: flex;
                  align-items: center;
                  position: relative;
                  p{
                    font: 1em/1.125em "Arial";
                    font-weight: bold;
                    color: #777;
                  }
                  select{
                    padding: 0 10px;
                    height: 25px;
                    font: .938em/25px "Arial";
                    color: #434343;
                    background-color: #eaeaea;
                    border: 1px solid #bdbdbd;
                    outline: none;
                    -webkit-appearance: none;
                    -moz-appearance: none;
                    -ms-appearance: none;
                    appearance: none!important;
                    &:hover{
                      cursor: pointer;
                    }
                  }
                  select::-ms-expand{
                    display: none;
                  }
                  i{
                    position: absolute;
                    top: 7px;
                  }
                }
                .sort{
                  select{
                    margin: 0 15px;
                    width: 175px;
                  }
                  i{
                    right: 25px;
                  }
                }
                .quantity{
                  select{
                    margin: 0 0 0 15px;
                    width: 50px;
                  }
                  i{
                    right: 7px;
                  }
                }
              }
            }
            &__cart{
              margin: 15px 0;
              width: 100%;
              display: flex;
              flex-wrap: wrap;
              justify-content: space-between;
              .cart{
                margin: 15px 0;
                width: 30%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                position: relative;
                border: 1px solid #dfdfdf;
                a{
                  display: block;
                  text-decoration: none;
                  position: relative;
                  .freeShiping,
                  .onSale{
                    display: inline-block;
                    position: absolute;
                    font: 12px/14px "Arial";
                    color:#fff;
                    text-align: center;
                    background-color: #ff0000;
                    transform: rotate(-45grad);
                    &:after{
                      content: "";
                      width: 0;
                      height: 0;
                      position: absolute;
                      top: -1px;
                      left: -1px;
                    }
                    &:before{
                      content: "";
                      width: 0;
                      height: 0;
                      position: absolute;
                      top: -1px;
                      right: -1px;
                    }
                  }
                  .freeShiping{
                    width: 80px;
                    top: 1px;
                    left: -19px;
                    &:after{
                      border-top: 29px solid #fff;
                      border-right: 25px solid transparent;
                    }
                    &:before{
                      border-top: 29px solid #fff;
                      border-left: 34px solid transparent;
                    }
                  }
                  .onSale{
                    width: 60px;
                    top: 7px;
                    left: -12px;
                    &:after{
                      border-top: 15px solid #fff;
                      border-right: 13px solid transparent;
                    }
                    &:before{
                      border-top: 15px solid #fff;
                      border-left: 17px solid transparent;
                    }
                  }
                  img{
                    width: 100%;
                    padding: 15px 20px;
                  }
                  p{
                    padding: 15px 20px;
                    min-height: 100px;
                    display: block;
                    font: .875em/1em "Arial";
                    font-weight: bold;
                    color: #0081ef;
                  }
                }
                ul{
                  display: flex;
                  li{
                    a{
                      display: block;
                      font: .875em/40px "Arial";
                      font-weight: bold;
                      color: #777;
                      text-align: center;
                      background-color: #eee;
                      border-top: 1px solid #dfdfdf;
                    }
                  }
                  .inCart{
                    width: 61%;
                  }
                  .heart,
                  .exchange{
                    width: 50px;
                    border-left: 1px solid #dfdfdf;
                  }
                }
                .hover{
                  display: none;
                  width: 254px;
                  position: absolute;
                  bottom: -149px;
                  left: -1px;
                  background-color: #fff;
                  border: 1px solid #78afeb;
                  border-top: none;
                  box-shadow: 5px 5px 15px #cecece;
                  z-index: 5;
                  .descr{
                    padding: 10px 20px;
                    display: flex;
                    flex-direction: column;
                    li{
                      p{
                        font: .875em/1.375em "Arial";
                        font-weight: bold;
                        span{
                          padding: 0 0 0 5px;
                          font-weight: normal;
                        }
                      }
                    }
                  }
                  .about{
                    padding: 0 20px 15px;
                    font: .875em/1em "Arial";
                    color: #636363;
                  }
                }
                &:hover{
                  border-color: #78afeb;
                  border-bottom-color: #dfdfdf;
                  box-shadow: 5px 5px 15px #cecece;
                  .hover{
                    display: block;
                  }
                }
              }
            }
            &__pagination{
              margin: 150px 0 0;
              display: flex;
              justify-content: center;
              align-items: flex-end;
              ul{
                display: flex;
                justify-content: center;
                li{
                  margin: 0 5px;
                  width: 45px;
                  height: 40px;
                  background-color: #efefef;
                  border: 1px solid #dedede;
                  span{
                    display: block;
                    font: 1.438em/40px "Arial";
                    font-weight: bold;
                    color: #0081ef;
                    text-align: center;
                    i{
                      font-weight: bold;
                    }
                    &:hover{
                      cursor: pointer;
                    }
                  }
                }
                .active{
                  background-color: #fff;
                  border-color: #0081ef;
                }
              }
              .showMore{
                width: 350px;
                display: none;
                font: 1.188em/40px "Arial";
                font-weight: bold;
                color: #0081ef;
                text-align: center;
                text-transform: uppercase;
                text-decoration: none;
                background-color: #efefef;
                border: 1px solid #dedede;
                &:hover{
                  background-color: #fff;
                }
              }
            }
          }
        }
        .col-sm-12{
          .aside{
            margin: 50px 0 0;
            display: none;
          }
        }
      }
    }
  }
  #customization{
    width: 130px;
    height: 30px;
    display: none;
    position: fixed;
    top: 200px;
    right: 5%;
    font: 14px/30px "Arial";
    font-weight: bold!important;
    color: #777a7d;
    text-align: center;
    text-decoration: none;
    background-color: #f2f2f2;
    border: 1px solid #a0a0a0;
  }
}